import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Tabletop from "tabletop";

import "./homeContentSection.css";
import { AdsHorizontal, AdsVerticalHomeContent } from "../adsMethods";
import { googleSheetKey } from "../../config/googleSheetMethod";

import image01 from "../../storages/images/content/needforspeedmostwanted.jpg";
import image02 from "../../storages/images/content/maxpayne3.jpg";
import image03 from "../../storages/images/content/halo3.jpg";
import image04 from "../../storages/images/content/fruitninja.jpg";

//import page20210602x01 from "../../storages/content/dau-truong-chan-ly-hoc-hoi-nhung-meo-doc-khi-choi-sett-one-punch-man-tu-ky-thu-top-8-thach-dau";

function ContentItem(props) {
  return (
    <div className="item">
      <a href={props.link}>
        <img src={props.src} alt={props.alt} class="img_inner"></img>
      </a>
      <div className="itemContent">
        <a href={props.link}>
          <h3>{props.title}</h3>
        </a>
        <div className="timestamp">
          <h5>{props.types}</h5>
          <div className="time">{props.time}</div>
        </div>
        <p>{props.content}</p>
      </div>
    </div>
  );
}

export default class HomeContentSection extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    Tabletop.init({
      key: googleSheetKey,
      callback: (googleData) => {
        this.setState({
          data: googleData,
        });
      },
      simpleSheet: true,
    });
  }

  render() {
    console.log("updated state --->", this.state);
    const { data } = this.state;
    return (
      <div className="homeContent">
        <div className="containerContent">
          <AdsHorizontal />
          <div className="content">
            {data.map((obj) => {
              return (
                <ContentItem
                  title={obj.title}
                  content={obj.content}
                  src={obj.src}
                  alt={obj.alt}
                  types={obj.types}
                  time={obj.time}
                  link={obj.link}
                />
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}
